import Axios from 'axios'
import { useEffect } from 'react'
import { connect } from 'react-redux'

// COMPONENTS
import TableData from './TableData'
import BuyList from './BuyList'
import Fav from './Fav'
import Remind from './Remind'
import Trash from './Trash'


const PantryApp = (props) => {

    useEffect (() => {
        Axios('http://localhost:8000/lists')
            .then(res => {
                props.setLists(res.data);
        })
    }, [])

    useEffect (() => {
        Axios('http://localhost:8000/products')
            .then(res => {
                props.setProducts(res.data);
        })
    }, [])


    return ( 
        <>
        <div className="header">
                <h1>Home Supply Management</h1>
        </div>

            < TableData/>

        <div className="leftSide">

            
            <BuyList />

            
            <Fav />

            
            <Trash />

            

            <Remind />
            

        </div>
        </>
    )
}

const mapStateToProps = (state) => {
    return {
        lists: state.lists,
        products: state.products
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        setLists: (lists) => dispatch({type: 'SET_LISTS', payload: lists}),
        setProducts: (product) => dispatch({type: 'SET_PRODUCTS', payload: product})
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(PantryApp)