// ICONS
import  add  from '../icons/005-plus.png'

// 
import { connect } from 'react-redux'
import Axios from 'axios'



const TrashList = (props) => {

    let addBtn1 = () => {
        Axios.put('http://localhost:8000/lists/' + props.list._id, {home: false,
            delete: false,
            fav: false,
            buy: true})
        .then (res => {
            props.usedToBuy(res.data)
        })
    }
    
    return (
        <>
        <ul className="trashList">
            <li className="trashListed">
            {props.list.name}
            </li>
            <i onClick={addBtn1}>
                <img 
                className="checkIcon4"
                 src={add}/>
            </i>
        </ul>
        </>
    )
}

const mapDispatchToProps = ( dispatch ) => {
    return  {
        usedToBuy: (list) => dispatch ({type: 'USED_TO_BUY', payload: list }),
    }
}

export default connect(null, mapDispatchToProps)(TrashList);