// 
import DatePicker from 'react-date-picker'
import  { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import Axios from 'axios'

const   AddReminderForm = (props) =>  {

    let [reminderMsg, setReminderMsg] = useState('')
    let [remindAt, setRemindAt] = useState()

    let remindMe = () =>    {
        Axios.post('http://localhost:8000/reminders/', {reminderMsg, remindAt})
        .then (res => {
            props.addReminder(res.data)
            setReminderMsg('')
            setRemindAt()
        })
    }
    return  (
        <>
        <div className="addReminderForm">
            <div>
                <textarea
                className="textArea"
                type="text"
                placeholder="Remind me this...."
                value={reminderMsg}
                cols= "25"
                rows=  "3"
                onChange={e => setReminderMsg(e.target.value)}
                />
            </div>
            <div className="addReminder2">
                <DatePicker
                className="dateTimePickerReminder"
                value={remindAt}
                onChange={setRemindAt}
                minDate={new Date()}
                dayPlaceholder="DD"
                monthPlaceholder="MM"
                yearPlaceholder="YYYY"
                />
                <div>
                    <button
                    className="remindMe"
                    onClick={remindMe}
                    ><b>REMIND ME</b></button>
                </div>
            </div>
        </div>
        </>
    )
}

const mapDispatchToProps = ( dispatch ) => {
    return  {
        addReminder: (reminder) => dispatch ({type: 'ADD_REMINDER', payload: reminder }),
    }
}

export default connect(null, mapDispatchToProps)(AddReminderForm)