// ICONS
import  clse  from '../icons/007-minus.png'
import cross from '../icons/008-cross.png'

// COMPONENT
import FavLIst from './FavLIst'

// 
import { useState } from 'react'
import { connect } from 'react-redux'

const   Fav = (props) => {
    let [active, setActive] = useState(true)

    let hideShow = () => {
        if  (active === true) {
            setActive(false)
        } else {
            setActive(true)
        }
    }

    let starList = props.lists.filter( list => list.fav === true )

    if (active) {
    return  (
        <>

        <div className="favBox1">
            <div className="favName">
                <h3>Favorite Item</h3>
                <i onClick={hideShow}>
                    <img 
                    className="closeIcon2"
                    src={clse}/>
                </i>
            </div>
        </div>

        </>
    )
    } else {
        return  (
        <>

        <div className="favBox">

            <div className="favName">
                <h3>Favorite Item</h3>
                <i onClick={hideShow}>
                    <img 
                    className="closeIcon2"
                    src={cross}/>
                </i>
            </div>

            {/* {props.starList.length !== 0 ? */}

                <div className="favContent">
                    {starList.map( list =>
                    <FavLIst
                    list={list} />
                    )} 
                </div>
                    {/* // : */}
                    {/* <h2>Empty</h2> */}
                {/* // }  */}
                </div>

        </>
        )
    }
}

const mapStateToProps = (state) =>   {
    return {
        lists: state.lists,
    }
}

export default connect(mapStateToProps)(Fav)