//  ICONS
import  check  from '../icons/001-check.png'

// 
import Axios from 'axios'
import { connect } from 'react-redux'


const BuyListItem = (props) => {

    let listedAgain = () => {
        Axios.put('http://localhost:8000/lists/' + props.list._id, {home: true,
            delete: false,
            fav: false,
            buy: false})
        .then (res => {
            props.toBuyItem(res.data)
        })
    }
    
    return (
        <>

        <ul className="buyList">
            <li className="buyListed">
                {props.list.name}
            </li>
            <i onClick={listedAgain}>
                <img 
                className="checkIcon2" 
                src={check}/>
            </i>
        </ul>

        
        </>
    )
}

const mapDispatchToProps = ( dispatch ) => {
    return  {
        toBuyItem: (list) => dispatch ({type: 'TO_BUY_ITEM', payload: list }),
     }
}

export default connect(null, mapDispatchToProps)(BuyListItem)