// ICONS
import  add  from '../icons/005-plus.png'
import  dlt  from '../icons/003-trash-can.png'

// 
import Axios from 'axios'
import { connect } from 'react-redux'


const FavLIst = (props) =>  {

    let favToBuy = () => {
        Axios.put('http://localhost:8000/lists/' + props.list._id, {home: false,
            delete: false,
            fav: false,
            buy: true})
        .then (res => {
            props.favBuy(res.data)
        })
    }

    let remove = () => {
        Axios.put('http://localhost:8000/lists/' + props.list._id, {home: false,
            delete: true,
            fav: false,
            buy: false})
        .then (res => {
            props.removeFav(res.data)
        })
    }
    return (
        <>
        
        <ul className="favList">
            <li className="favListed">
                {props.list.name}
            </li>
            <i onClick={favToBuy}>
                <img 
                className="checkIcon3" src={add}/>
            </i>
            <i onClick={remove}>
                <img 
                className="dltIcon2"
                src={dlt}/>
            </i>
        </ul>

        </>
    )
}

const mapDispatchToProps = ( dispatch ) => {
    return  {
        favBuy: (list) => dispatch ({type: 'FAVORITE_TO_BUY', payload: list }),
        removeFav: (list) => dispatch ({type: 'REMOVE_FAV', payload: list }),
    }
}

export default connect(null,mapDispatchToProps)(FavLIst);