// ICONS
import cross from '../icons/008-cross.png'
import note from '../icons/012-sticky-notes.png'

// COMPONENTS
import ReminderList from './ReminderList'
import AddReminderForm from './AddReminderForm'

// 
import  { useState, useEffect, Fragment } from 'react'
import { connect } from 'react-redux'
import Axios from 'axios'
import EditReminder from './EditReminder'

    

const   Remind = (props) => {

    // HIDE AND SHOW
    let [active, setActive] = useState(true)

    let hideShow = () => {
        if  (active === true) {
            setActive(false)
        } else {
            setActive(true)
        }
    }

    // REMINDER
    useEffect (() => {
        Axios('http://localhost:8000/reminders')
            .then(res => {
                props.setReminder(res.data);
        })
    }, [])

    let [reminderMsg, setReminderMsg] = useState('')
    let [remindAt, setRemindAt] = useState()

    
    let [editReminderMsg, setEditReminderMsg] = useState(null)
    let reminder = props.reminders

    let handleEditReminder = (e, reminder) =>   {
        e.preventDefault();
        setEditReminderMsg(reminder._id)
    }

    if (active) {
        return (
            <>

            <div className="reminderBox1">

                <div className="reminderName">
                    <h3>REMINDER</h3>
                    <i onClick={hideShow}>
                        <img 
                        className="closeIcon3"
                        src={note}/>
                    </i>
                </div>

            </div>

            </>
        )
    } else {
        return  (
            <>

            <div className="reminderBox">

                <div className="reminderName">
                    <h3>REMINDER</h3>
                    <i onClick={hideShow}>
                        <img 
                        className="closeIcon3"
                        src={cross}/>
                    </i>
                </div>

                <div className="reminderContent">

                    <AddReminderForm />

                </div>
                <div>
                    {props.reminders.map( reminder =>

                    <Fragment>

                        {editReminderMsg === reminder._id ? 

                        (<EditReminder 
                            reminder={reminder}/>) 
                            : 
                        (< ReminderList 
                            reminder={reminder}
                            handleEditReminder={handleEditReminder}/>)}
                        
                    </Fragment>
                        )}
                </div>

            </div>
            
            </>
        )
    }
}

const mapStateToProps = (state) =>  {
    return {
        reminders: state.reminders
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        setReminder: (reminder) => dispatch({type: 'SET_REMINDERS', payload: reminder}),
        addReminder: (reminder) => dispatch ({type: 'ADD_REMINDER', payload: reminder }),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Remind);