const initialState = {
    lists: [],
    products: [],
    reminders: []
}
const reducer = (state = initialState, action) => {
    
    switch(action.type) {
        case    "SET_LISTS": {
                return {
                    ...state,
                    lists: action.payload
                }
        }
        case    "SET_REMINDERS": {
                return  {
                    ...state,
                    reminders: action.payload
                }
        }
        case    "SET_PRODUCTS": {
                return {
                    ...state,
                    products: action.payload
                }
        }
        case    "ADD_LIST": {
                let listCopy2 = [...state.lists, action.payload]
                return  {
                    ...state,
                    lists: listCopy2
                }
        }
        case    "ADD_REMINDER": {
                let reminderCopy = [...state.reminders, action.payload]
                return  {
                    ...state,
                    reminders: reminderCopy
                }
        }
        case    "EDIT_LIST": {
                let editList = [...state.lists]
                let index = editList.findIndex ( (list) => list._id === action.payload._id)
                if ( index !== -1 ) {
                    editList[index] = action.payload
                    console.log(editList)
                    return  {
                        ...state,
                        lists: editList
                    }

                }
        }
        case    "EDIT_REMINDER": {
                let reminderCopy3 = [...state.reminders]
                let index = reminderCopy3.findIndex( (reminder) => reminder._id === action.payload._id)
                if ( index !== -1 ) {
                    reminderCopy3[index].reminderMsg = action.payload.reminderMsg
                    reminderCopy3[index].remindAt = action.payload.remindAt
                    return  {
                        ...state,
                        reminders: reminderCopy3
                    }
                }
        }
        case    "DEL_LIST": {
                let listCopy = [...state.lists]
                let index = listCopy.findIndex( (list) => list._id === action.payload._id);
                if ( index !== -1 )
                listCopy.splice(index, 1)
                return  {
                    ...state,
                    lists: listCopy,
                }
        }
        case    "DEL_REMINDER": {
                let reminderCopy2 = [...state.reminders]
                let index = reminderCopy2.findIndex( (reminder) => reminder._id === action.payload._id);
                if ( index !== -1 )
                reminderCopy2.splice(index, 1)
                return  {
                    ...state,
                    reminders: reminderCopy2
                }
        }
        case    "CHECK_LIST": {
                let listCopy3 = [...state.lists]
                let index = listCopy3.findIndex((list) => list._id === action.payload._id)
                if (index !== -1)
                listCopy3[index].home = false
                listCopy3[index].delete = true
                return {
                    ...state,
                    lists: listCopy3
                }
        }
        case    "USED_TO_BUY":  {
                let listCopy5 = [...state.lists]
                let index = listCopy5.findIndex((list) => list._id === action.payload._id)
                if (index !== -1)
                listCopy5[index].buy = true
                listCopy5[index].delete = false
                return {
                    ...state,
                    lists: listCopy5
                }
        }
        case    "FAV_LIST": {
                let listCopy4 = [...state.lists]
                let index = listCopy4.findIndex((list) => list._id === action.payload._id)
                if (index !== -1)
                listCopy4[index].fav = true
                return {
                    ...state,
                    lists: listCopy4
                }
        }
        case    "FAVORITE_TO_BUY":  {
                let listCopy6 = [...state.lists]
                let index = listCopy6.findIndex((list) => list._id === action.payload._id)
                if (index !== -1)
                listCopy6[index].fav = true
                listCopy6[index].buy = true
                return {
                    ...state,
                    lists: listCopy6
                }
        }
        case    "REMOVE_FAV":   {
                let listCopy7 = [...state.lists]
                let index = listCopy7.findIndex((list) => list._id === action.payload._id)
                if (index !== -1)
                listCopy7[index].fav = false
                listCopy7[index].delete = true
                return {
                    ...state,
                    lists: listCopy7
                }
        }
        case    "TO_BUY_ITEM":  {
                let listCopy8 = [...state.lists]
                let index = listCopy8.findIndex((list) => list._id === action.payload._id)
                if (index !== -1)
                listCopy8[index].buy = false
                listCopy8[index].home = true
                return {
                    ...state,
                    lists: listCopy8
                }
        }
        default:
            return state;
    }
}

export default reducer;