// ICONS
import  clse  from '../icons/007-minus.png'
import cross from '../icons/008-cross.png'

// COMPONENTS
import TrashList from './TrashList'

// 
import  { useState } from   'react'
import { connect } from 'react-redux'

const   Trash = (props) => {
    let [active, setActive] = useState(true)

    let hideShow = () => {
        if  (active === true) {
            setActive(false)
        } else {
            setActive(true)
        }
    }

    let doneList = props.lists.filter( list => list.delete === true )

    if ( active ) {
        return  (
            <>

            <div className="trashBox1">
                <div className="trashName">
                    <h3>Used Item</h3>
                    <i onClick={hideShow}>
                        <img 
                        className="closeIcon4"
                        src={clse}/>
                    </i>
                </div>
            </div>

            </>
        )
    } else {
        return  (
            <>

            <div className="trashBox">
                <div className="trashName">
                    <h3>Used Item</h3>
                    <i onClick={hideShow}>
                        <img 
                        className="closeIcon4"
                        src={cross}/>
                    </i>
                </div>
                <div className="trashContent">
                    <ul className="trashList">
                        <li className="trashListed">
                            {doneList.map( list =>
                            <TrashList 
                            list={list}/>)}
                        </li>
                    </ul>
                </div>
            </div>

            </>
        )
    }
}

const mapStateToProps = (state) => {
    return  {
        lists: state.lists
    }
}

const mapDispatchToProps = ( dispatch ) => {
    return  {
        clearBtn: () => dispatch ({type: "CLEAR_ALL"})
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Trash);