// ICONS
import  check  from '../icons/001-check.png'
import  hrt  from '../icons/009-favorite.png'
import  edit  from '../icons/002-edit.png'
import  dlt  from '../icons/003-trash-can.png'

// 
import { connect } from 'react-redux'
import Axios from 'axios'


const   TableRowData = (props) =>   {
    
    let  {name, qty, expiryDate, notes, _id} = props.list
    let list = props.list
    
    let starIcon = () => {
        Axios.put('http://localhost:8000/lists/' + _id, { 
            home: true,
            delete: false,
            fav: true,
            buy: false})
        .then (res => {
            props.favoriteList(res.data)
        })
    }

    let checkIcon = () => {
        Axios.put('http://localhost:8000/lists/' + _id, {home: false,
            delete: true,
            fav: false,
            buy: false})
        .then (res => {
            props.checkList(res.data)
        })
    }

    let dltIcon = () => {
        Axios.delete('http://localhost:8000/lists/' + _id)
        .then(res => {
            props.dltList(res.data)
        })
    }

    return  (
        <>
            <tr>
                <td className="tData">{name}</td>
                <td className="tData">{qty}</td>
                <td className="tData">{expiryDate}</td>
                <td className="tData">{notes}</td> 

                <td className="tDataBtn">
                    <div>
                        <i onClick={starIcon}>
                            <img 
                            className="heart"
                            src={hrt}/>
                        </i>
                    </div>
                    <div>
                        <i onClick={checkIcon}>
                        <img 
                        className="checkIcon1"
                        src={check}/>
                        </i>
                    </div>
                    <div>
                        <i onClick={(e) => props.handleEditBtn(e, list)}>
                            <img 
                            className="editIcon1"
                            src={edit}/>
                        </i>
                    </div>
                    <div>
                        <i onClick={dltIcon}>
                            <img 
                            className="dltIcon1"
                            src={dlt}/>
                        </i>   
                    </div>
                </td> 
            </tr>
        </>
    )
}


const mapDispatchToProps = ( dispatch ) => {
    return  {
        dltList: (list) => dispatch ({type: 'DEL_LIST', payload: list }),
        checkList: (list) => dispatch ({type: 'CHECK_LIST', payload: list }),
        favoriteList: (list) => dispatch ({type: 'FAV_LIST', payload: list })
    }
}

export default connect(null, mapDispatchToProps)(TableRowData);