// 
import DatePicker from 'react-date-picker'
import  { useState} from 'react'
import Axios from 'axios'
import  { connect } from 'react-redux'

const EditReminder = (props) => {

    let [editReminderMsg, setEditReminderMsg] = useState('')
    let [editRemindAt, setEditRemindAt] = useState()

    let reminder = props.reminders
    
    let updateReminderBtn = (e) => {
        Axios.put('http://localhost:8000/reminders/' + props.reminders._id,
         reminder)
        .then (res => {
            props.updatedReminder(res.data)
        })
    }

    return  (
        <>
        <div>
            <div className='reminderMsg'>
                <textarea 
                type="text"
                placeholder={props.reminder.reminderMsg}
                cols= "30"
                rows=  "3"
                value={editReminderMsg}
                onChange={e => setEditReminderMsg(e.target.value)}
                />
                {/*  */}
            </div>
            <div className='remindAt'>
                <DatePicker 
                value={editRemindAt}
                onChange={setEditRemindAt}
                minDate={new Date()}
                dayPlaceholder="DD"
                monthPlaceholder="MM"
                yearPlaceholder="YYYY"
                />
            </div>
            <div>
                <button
                onClick={updateReminderBtn}
                >UPDATE ME</button>
            </div>
        </div>
        </>
    )
}

const mapStateToProps = (state) =>  {
    return {
        reminders: state.reminders
    }
}
const   mapDispatchToProps = (dispatch) =>  {
    return  {
        updatedReminder: (reminder) => dispatch ({type: 'EDIT_REMINDER', payload: reminder})
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(EditReminder)