// ICONS
import  edit  from '../icons/002-edit.png'
import  dlt  from '../icons/003-trash-can.png'

// 
import Axios from 'axios'
import { connect } from 'react-redux'

const   ReminderList = (props) => {
    
    let reminder = props.reminder

    let dltReminderIcon = () => {
        Axios.delete('http://localhost:8000/reminders/' + props.reminder._id)
        .then(res => {
            props.dltReminder(res.data)
        })
    }

    return  (
        <>
        <div className="noteContainer">
            <div className='reminderMsg'>
                {props.reminder.reminderMsg}
            </div>
            <div className='remindAt'>
                {props.reminder.remindAt}
            </div>
            <div className="reminderIcons">
                <i onClick={dltReminderIcon}>
                    <img 
                    className="dltIcon3"
                    src={dlt}/>
                </i>
                <i onClick={(e) => props.handleEditReminder(e, reminder)}>
                    <img 
                    className="editIcon2"
                    src={edit}/>
                </i>
                <hr/>
            </div>
        </div>

        </>
    )
}

const mapDispatchToProps = ( dispatch ) => {
    return  {
        dltReminder: (reminder) => dispatch ({type: 'DEL_REMINDER', payload: reminder})
    }
}

export default connect(null, mapDispatchToProps)(ReminderList);