// ICONS
import  add  from '../icons/006-add.png'


// 
import { useState } from 'react'
import { connect } from 'react-redux'
import Axios from 'axios'
import DatePicker from 'react-date-picker'


const AddListForm = (props) =>  {

    let [name, setName] = useState('')
    let [qty, setQty] = useState('')
    let [expiryDate, setExpiryDate] = useState('')
    let [notes, setNotes] = useState('')


    let changeName = (e) => {
        setName(e.target.value)
    }
    let changeQty = (e) => {
        setQty(e.target.value)
    }
    // let changeExpiry = (e) => {
    //     setExpiryDate(e.target.value)
    // }
    let changeNote = (e) => {
        setNotes(e.target.value)
    }
    
    let handleSubmit = (e) => {
        Axios.post('http://localhost:8000/lists/', {name, qty, expiryDate, notes})
        .then (res => {
            props.addList(res.data)
        })
    }

    return (
        <>
        <div className="addNav">


        <i 
        className="addIcon"
        onClick={handleSubmit}>
            <img 
            className="addListIcon"
            src={add}/>
        </i>
        <div >
            <form  className="addForm">
                <input 
                className="addName"
                type="text"
                placeholder="Enter Name"
                value={name}
                onChange={changeName} />

                <input 
                className="addQty"
                min="1"
                type="number"
                placeholder="1"
                value={qty}
                onChange={changeQty} />

                    <DatePicker 
                    className="addDate"
                    value={expiryDate}
                    onChange={setExpiryDate}
                    dayPlaceholder="DD"
                    monthPlaceholder="MM"
                    yearPlaceholder="YYYY"
                    />

                <input 
                className="addNote"
                type="text"
                placeholder="Write a note.."
                value={notes}
                onChange={changeNote} />
            </form>
        </div>

        </div>
        </>
    )
}

const   mapDispatchToProps = (dispatch) =>  {
    return  {
        addList: (list) => dispatch ({ type:'ADD_LIST', payload: list})
    }
}

export default connect(null, mapDispatchToProps)(AddListForm)