// 
import { useState } from 'react'
import { connect } from 'react-redux'
import Axios from 'axios'
import DatePicker from 'react-date-picker'


const EditForm = (props) => {
    let [editName, setEditName] = useState('')
    let [editQty, setEditQty] = useState('')
    let [editExpiryDate, setEditExpiryDate] = useState('')
    let [editNotes, setEditNotes] = useState('')

    let updateName = (e) => {
        setEditName(e.target.value)
    }
    let updateQty = (e) => {
        setEditQty(e.target.value)
    }
    let updateNote = (e) => {
        setEditNotes(e.target.value)
    }
    
    let list = props.lists
    
    let handleUpdate = (e) => {
        Axios.put('http://localhost:8000/lists/' + props.lists._id, {
            name: editName,
            qty: editQty,
            expiryDate: editExpiryDate,
            notes: editNotes
        })
        .then (res => {
            props.editList(res.data)
        })
    }

    return  (
        <>
        <tr>
            <td className="tData">
                <input 
                type="text"
                placeholder={props.list.name}
                value={editName}
                onChange={updateName} />
            </td>

            <td className="tData">
                <input 
                type="number"
                placeholder={props.list.qty}
                value={editQty}
                onChange={updateQty} />
            </td>
            
            <td className="tData expiryDate">
                <DatePicker
                value={editExpiryDate}
                onChange={setEditExpiryDate}
                minDate={new Date()}
                dayPlaceholder="DD"
                monthPlaceholder="MM"
                yearPlaceholder="YYYY"
                />
            </td>

            <td className="tData">
                <input 
                type="text"
                placeholder={props.list.notes}
                value={editNotes}
                onChange={updateNote} />
            </td>
            <td>
                <button
                onClick={handleUpdate}>
                    Update
                </button>
            </td>
        </tr>
        
        </>
    )
}

const   mapDispatchToProps = (dispatch) =>  {
    return  {
        editList: (list) => dispatch ({ type:'EDIT_LIST', payload: list})
    }
}

export default connect(mapDispatchToProps)(EditForm)