// ICONS
import  clse  from '../icons/007-minus.png'
import cross from '../icons/008-cross.png'

// CONTENT
import BuyListItem from './BuyListItem'

// 
import  { useState, useEffect} from 'react'
import { connect } from 'react-redux'
import Axios from 'axios'

const   BuyList = (props) => {
    let [active, setActive] = useState(true)

    let hideShow = () => {
        if  (active === true) {
            setActive(false)
        } else {
            setActive(true)
        }
    }

    // useEffect (() => {
    //     Axios('http://localhost:8000/products')
    //         .then(res => {
    //             props.setProducts(res.data);
    //     })
    // }, [])

    let toBuyList = props.lists.filter( list => list.buy === true )

    if ( active ) {
    return  (
        <>

        <div className="buyBox1">
            <div className="buyName">
                <h3>To Buy List</h3>
                <i
                onClick={hideShow}>
                    <img 
                    className="closeIcon1"
                    src={clse}/>
                </i>
            </div>
        </div>

        </>
    )
    } else {
    return  (
        <>

        <div className="buyBox">
            <div className="buyName">
                <h3>To Buy List</h3>
                <i
                onClick={hideShow}>
                    <img 
                    className="closeIcon1"
                    src={cross}/>
                </i>
            </div>
            <div className="buyContent">

                {toBuyList.map( list => 
                <BuyListItem
                list={list} /> )}

            </div>
        </div>

        </>
        )
    }
}

const mapStateToProps = (state) => {
    return  {
        products: state.products,
        lists: state.lists,

    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        setProducts: (product) => dispatch({type: 'SET_PRODUCTS', payload: product})
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(BuyList)